import base64 from 'base-64';
import {getToken} from './authenticate.js';
import {gql, GraphQLClient} from 'graphql-request';
import config from '../../lib/config.json';

export const getEndTime = async (token, code) => {
	const url = config.API_URL;

	const query = gql`
	{
		reportData {
			report(code: "${code}") {
				code
				title
				endTime
			}
		}
	}`;

	const client = new GraphQLClient(url, {headers: {"Authorization": `Bearer ${token}`}})
	return client.request(query);
}

export const getSourceID = async (token, endTime, code) => {
	const url = config.API_URL;

	const query = gql`
	{
		reportData {
			report(code: "${code}") {
				playerDetails(killType: Trash, startTime: -1, endTime: ${endTime})
			}
		}
	}`;

	const client = new GraphQLClient(url, {headers: {"Authorization": `Bearer ${token}`}})
	return client.request(query);
}


export const getDeaths = async (token, endTime, encounterType, sourceID, code) => {
	const url = config.API_URL;

	const query = gql`
	{
		reportData {
			report(code: "${code}") {
				events (killType: ${encounterType}, startTime: -1, endTime: ${endTime}, dataType: Deaths, sourceID: ${sourceID}) {
					data
				}
			}
		}
	}`;

	const client = new GraphQLClient(url, {headers: {"Authorization": `Bearer ${token}`}})
	return client.request(query);
}

export const getDeathDetails = async (token, endTime, sourceID, fightID, code) => {
	const url = config.API_URL;

	const query = gql`
	{
		reportData {
			report(code: "${code}") {
				code
				title
				table (fightIDs: ${fightID}, startTime: -1, endTime: ${endTime}, dataType: Deaths, sourceID: ${sourceID})
			}
	}`;

	const client = new GraphQLClient(url, {headers: {"Authorization": `Bearer ${token}`}})
	return client.request(query);
}
