import base64 from 'base-64';
import {getToken} from './authenticate.js';
import config from '../../lib/config.json';
import {gql, GraphQLClient} from 'graphql-request';

export const getCharacterData = async (token) => {
	const url = config.API_URL;

	const query = gql`
	{
		characterData {
			character(name: "Amrak", serverSlug: "Whitemane", serverRegion: "us") {
				canonicalID
				classID
				id
				recentReports {
					current_page
					data {
						code
					}
				}
			}
		}
	}`;

	const client = new GraphQLClient(url, {headers: {"Authorization": `Bearer ${token}`}})
	return client.request(query);
}
