import base64 from 'base-64';
import {getToken} from '../api/routes/authenticate.js';
import {getCharacterData} from '../api/routes/characterData.js';
import {getEndTime, getSourceID, getDeaths, getDeathDetails} from '../api/routes/reportData.js';
import {gql, GraphQLClient} from 'graphql-request';
import config from '../lib/config.json';
import React, { Component } from 'react';
import './deaths.css'

class Deaths extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
          totalDeathsNum: null,
          trashDeathsNum: null,
          encounterDeathsNum: null,
          link: null
    }
  }

    render() {
        return (
            <div className="Deaths-counter">
                {this.state.totalDeathsNum}
                <div className="Deaths-breakdown">
                    Trash: {this.state.trashDeathsNum}, Boss: {this.state.encounterDeathsNum}
                </div>
                <a
                  className="Report-link"
                  href="this.state.link"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {this.state.link}
                </a>
            </div>)
            ;
    }

    async componentDidMount() {
        let token = "";
        let characterData = "";
        let endTimeData = "";
        let trashDeaths = "";
        let encounterDeaths = "";
        let sourceIDData = "";

        await getToken()
            .then(response => {
                token = response.access_token;
            })

        await getCharacterData(token)
            .then(response => {
                characterData = response;
            })

        let recentReportCode = characterData.characterData.character.recentReports.data[0].code;

        await getEndTime(token, recentReportCode)
            .then(response => {
                endTimeData = response;
            })

        let endTime = endTimeData.reportData.report.endTime;

        await getSourceID(token, endTime, recentReportCode)
            .then(response => {
                sourceIDData = response;
            })


        let tanks = sourceIDData.reportData.report.playerDetails.data.playerDetails.tanks;
        let sourceID = "";

        tanks.forEach(function (tank) {
            if (tank.name === "Amrak") {
                sourceID = tank.id;
            }
        })

        await getDeaths(token, endTime, "Trash", sourceID, recentReportCode)
            .then(response => {
                trashDeaths = response;
            })

        await getDeaths(token, endTime, "Encounters", sourceID, recentReportCode)
            .then(response => {
                encounterDeaths = response;
            })

        let trashDeathNum = trashDeaths.reportData.report.events.data.length;
        let encounterDeathNum = encounterDeaths.reportData.report.events.data.length;
        let deathsNum = trashDeathNum + encounterDeathNum;
        this.setState({ totalDeathsNum: deathsNum, trashDeathsNum: trashDeathNum, encounterDeathsNum: encounterDeathNum })
        this.setState({ link: `https://classic.warcraftlogs.com/reports/${recentReportCode}` })
    }
}

export default Deaths;